import React from 'react';

import './Preloader.css';

export function Preloader(props) {
  return (
    <>
      <div
        className={`preloader
    `}
      >
        <div className='circle-preloader_container'>
          <i className='circle-preloader'></i>
        </div>
        <p className='preloader__subtitle'>Searching for news...</p>
      </div>
    </>
  );
}
